<template>
    <div id="signin">
        <div>
            <input type="text" placeholder="Username" v-model="username"/>
            <input type="password" placeholder="Password" v-model="password"/>
        </div>
        <button v-on:click="login">Login</button>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'SignIn',
  data () {
    return {
      username: '',
      password: '',
      dismissSecs: 9999,
      dismissCountDown: 0,
      alertMessage: 'Request error'
    }
  },
  methods: {
    login () {
      axios.post('/wikirest/api/auth/login', { username: this.$data.username, password: this.$data.password })
        .then(response => {
          this.$store
            .dispatch('login', { token: response.data.token, username: response.data.username })

          this.$router.push('/articles')
        }, error => {
          this.$data.alertMessage = (error.response.data.message.length < 150) ? error.response.data.message : 'Request error. Please, report this error website owners'
          console.log(error)
        })
        .catch(e => {
          console.log(e)
          this.showAlert()
        })
    },
    countDownChanged (dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert () {
      this.dismissCountDown = this.dismissSecs
    }
  }
}
</script>

