<template>
    <div id="ArticleList">
        <div class="article-wrapper" v-for="article in articles" :key="article.id">
            <div><h3>{{ article.title }}</h3></div>
            <div>{{ article.user.username }}</div>
            <div>{{ article.postedDate }}</div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'ArticleList',
  data () {
    return {
      articles: []
    }
  },
  methods: {
    getArticleList () {
      const config = {
        headers: {
          Authorization: this.$store.getters.getToken
        }
      }

      axios
        .get('/wikirest/api/articles', config)
        .then(response => {
          this.articles = response.data
        }, error => {
          console.log(error)
        })
        .catch(e => {
          console.log(e)
        })
    }
  },
  mounted: function () {
    this.getArticleList()
  }
}
</script>

